import React from 'react';
import './chatWindow.css';
let ChatWindow = function(props){

    let socket = props.socket;
    console.log(socket)
    // socket.on("sentFromServerMessage",(data)=>{
    //     console.log(data)
    // })
    let formSubmit = (event)=>{
        event.preventDefault();

        console.log(event.target.messageValue.value)
        let MessageValue = String(event.target.messageValue.value);
        if(MessageValue !== ""){
            socket.emit("sentFromClientMessage",{text:MessageValue})
            event.target.messageValue.value = ""
            socket.on("sentFromServerMessage",(data)=>{
                console.log(data)
            })
        }
        

    }

    return <div className="chatWindowContainer">
        <div className="chatContents">
            <div className="chatContentEachMessage">Hello</div>
            <div className="chatContentEachMessage">Hello2</div>
            
        </div>
        <form className="chatSendContainer" onSubmit={formSubmit}>
            <div className="chatEmojiSelectionButton">😊</div>
            <div className="chatInputField">
                <input type="text" id="messageValue" placeholder="Start Typing ..." />
            </div>
            
            <div className="chatMicrophoneButton">
            
                <button>Send</button>
            
            </div>
        </form>
    </div>
}
export default ChatWindow