import React, { useState,useContext } from 'react';
import EachAvailableChat from './EachAvailableChat';
import UserContext from './../../../ContextAPIs/userContext';
import './Each.css';
let Availablechats = function(props){
    let data = useContext(UserContext);
    console.log(data)
    let stateArray = new Array()

    let [people,setPeople] = useState([
        {name:"PV",time:"08:55AM", lastMessage:'Hey What\'s happening!', profilePic:"https://avatars0.githubusercontent.com/u/48829314?s=460&u=f9ad5d29c13133b919427f27a5d6055bff5a0247&v=4"},
        {name:"SVNIT EE Chats",time:"Yesterday", lastMessage:'Meet me koi Join hua ?', profilePic:"https://upload.wikimedia.org/wikipedia/en/1/1b/NIT_Surat_Logo.svg"},
        {name:"Shlok Patel",time:"Yesterday", lastMessage:'Oii Project ka kya karega ?', profilePic:"https://avatars3.githubusercontent.com/u/51414879?s=460&u=3ea393bada1aa57c595bd47922875b8f148bc97f&v=4"},
        {name:"Shlok Patel",time:"Yesterday", lastMessage:'Oii Project ka kya karega ?', profilePic:"https://avatars3.githubusercontent.com/u/51414879?s=460&u=3ea393bada1aa57c595bd47922875b8f148bc97f&v=4"},
    ])
    data.name.data.forEach((element,index) => {
        console.log(element)
        stateArray.push({name:element.name,time:element.lastLoginTime,lastMessage:"849",profilePic:people[(index%people.length)].profilePic})
    });
    console.log(stateArray)
    // setPeople(prevState=>{
    //     return {}
    // })
    return <div style={{borderTop:"0.5px solid #dedede"}}>
        {stateArray.map((eachPeople,index)=>{
            return <EachAvailableChat key={index} data={eachPeople} />
        })}
    </div>
}
export default Availablechats