import React, { useState,useEffect, Component } from 'react';
import { Redirect } from 'react-router-dom';
import Availablechats from './AvailableChats/AvailableChats';
import ChatWindow from './Chats/ChatWindow';
import ChatHeader from './Header/ChatHeader';
import './home.css'
import MainProfile from './Profile/MainProfile';
import io from 'socket.io-client';

import {SOCKET_SERVER} from './../../settings/server';
import UserContext from '../../ContextAPIs/userContext';

class Home extends Component{
    constructor(props){
        super(props)
        this.state = {data:[]}
        console.log(props)
        console.log(UserContext)
        
       


    }
    componentDidMount(){
        this.socket = io(SOCKET_SERVER);
        console.log(this.socket)
        /* Sending to Register Current Socket to Email */
        this.socket.emit("setSocketIDToEmail",this.props.TriedLoggedData);
        this.socket.emit("getAllUsers","");

        this.socket.on("sentToClientMessage",(data)=>{
            console.log(data)
        })
        
        this.socket.on("allUsersTillDate",this.gotFunction)
        
    }
    gotFunction = (rawJSONData)=>{
        console.log(rawJSONData)
        // setUserState(rawJSONData)
        this.setState({data:rawJSONData},()=>{
            console.log(this.state)
        })
        
        
    }
    

    render(){
            if(localStorage.getItem("islogged") && localStorage.getItem("islogged")==="true" && 'email' in this.props.TriedLoggedData){

                

                

                return <div className="container">
                    <UserContext.Provider value={{name:this.state}}>
                        <div className="artificial">
                            <div className="titleapp">Clippy Chatter</div>
                            <div className="appContainer">
                                <div className="profile">
                                    <MainProfile />
                                </div>
                                <div className="header">
                                    <ChatHeader />
                                </div>
                                <div className="availablechats">
                                    <Availablechats />
                                </div>
                                <div className="chatwindow">
                                    <ChatWindow socket={this.socket} />
                                </div>
                            </div>
                        </div>
                    </UserContext.Provider>
            </div>
        }else{
            return <Redirect to="/" />
        }
    }
}
export default Home