import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import {REGISTERSERVER} from './../../settings/server';

const Login = ()=>{
    let [state,setState] = useState({email:"",pin:0,status:false})
    let updateEmail = (e)=>{
        setState(ps=>{
            return {...ps,email:e.target.value}
        })
    }
    let updatePin = (e)=>{
        setState(ps=>{
            return {...ps,pin:e.target.value}
        })
    }
    let doLogin = async (x)=>{
        x.preventDefault();
        console.log(state)
        if(state.email !== "" && state.pin !== 0){
            let response = await fetch(REGISTERSERVER,{method:"POST",mode:"cors",headers:{"Content-type":"application/json"},body:JSON.stringify(state)})
            let data = await response.json()
            console.log(data)
            setState((prevState)=>{
                return {...prevState,status:true}
            })
        }else{
            alert("Enter Something Correct")
        }
        
    }

    if(state.status === false){
            return <div>
            <form onSubmit={doLogin}>
                <input type="text" placeholder="Enter Email ID" onChange={updateEmail} />
                <input type="number" placeholder="Enter Pin" onChange={updatePin} defaultValue={0} />
                <button>submit</button>
            </form>
        </div>
    }else{
        localStorage.setItem("islogged","true")
        return <Redirect to={{pathname:"/home",state:{...state,from:'login'}}} />
    }
}
export default Login