import React from 'react'
let EachAvailableChat = function(props){
    return <div className="eachItem">
         <div className="eachProfilePic">
             <img src={props.data.profilePic} alt={props.data.profilePic} width="64px" />
         </div>
         <div className="eachMessageContent">
             <div className="eachMessageSenderName">{props.data.name}</div>
             <div className="eachMessageText">{props.data.lastMessage}</div>
             <div className="eachMessageTime">{props.data.time}</div>
         </div>
    </div>
}
export default EachAvailableChat